import React from 'react'

export class TaskAttribute extends React.Component {
  render() {
    return (
      <div>
        <h3>{this.props.task.approved_by}</h3>
        <h3>{this.props.task.description}</h3>
        <h3>{this.props.task.posted_on}</h3>
        <h3>{this.props.task.posted_by}</h3>
        <h3>{this.props.task.Estimated_effort}</h3>
        <h3>{this.props.task.Time_taken}</h3>
        <h3>{this.props.task.Level_of_commitment}</h3>
        <h3>{this.props.task.Experience_level}</h3>
        <h3>{this.props.task.Monetary_compensation}</h3>
        <h3>{this.props.task.Owner}</h3>
        <h3>{this.props.task.Assigned_consultancy}</h3>
        <h3>{this.props.task.reviewed}</h3>
        <h3>{this.props.task.Required_set_of_skills}</h3>
        <h3>{this.props.task.task_list}</h3>
        <h3>{this.props.task.applicant_list}</h3>
        <h3>{this.props.task.assigned_users}</h3> 
      </div>
      );
  }
}

export default TaskAttribute
