import React from 'react';
import TaskAttribute from './components/TaskAttribute';
const Axios = require('axios');


class App extends React.Component{
   
   
    constructor(props){
        super(props)
        this.state = {
            bala7 : []
        }
    }

    componentDidMount(){
      Axios.get('http://localhost:4000/api/task/' + this.props.match.params.id)
      .then(res => this.setState({bala7:res}))
      .then(res => console.log(res.status))
      .catch(err => {console.log(err)})
    }
    TaskAttributes() {
    return <TaskAttribute task={this.state.requiredTask} />;
    }
    
    render(){
        return <h1>{this.state.bala7.data.data}</h1>;
        //<TaskAttribute task = {this.state.requiredTask.data}/>;
    }
}
export default App